import Layout from 'components/Layout';
import type { PageProps } from 'gatsby';
import { useLanguage } from 'hooks/useLanguage';
import React, { useMemo } from 'react';
import { getLocalizedPath } from 'utils/i18nPath';
import BooksSection from './BooksSection';
import Jumbotron from './Jumbotron';
import RecentPostsSection from './RecentPostsSection';
import SteadySellersSection from './SteadySellersSection';

const HomePage = ({ data }: PageProps<Queries.HomePageQuery>) => {
  const { language: lang } = useLanguage();

  const books = useMemo(() => data.books.edges.map(e => e.node), [data]);
  const steadySellers = useMemo(() => data.steadySellers.edges.map(e => e.node), [data]);
  const posts = useMemo(() => data.posts.edges.map(e => e.node), [data]);

  const booksPath = getLocalizedPath('/books/', lang);
  const postsPath = getLocalizedPath('/posts/', lang);

  return (
    <Layout title={<Jumbotron />}>
      <BooksSection books={books} booksPath={booksPath} />
      <SteadySellersSection steadySellers={steadySellers} postsPath={postsPath} />
      <RecentPostsSection posts={posts} postsPath={postsPath} />
    </Layout>
  );
};

export default HomePage;
