import { Box } from '@mui/material';
import { useTheme } from '@mui/material/styles';
import React from 'react';

const JumbotronGrid = () => {
  const { palette } = useTheme();
  const gridColor = palette.mode === 'light' ? palette.text.primary : palette.common.white;
  const gridOpacity = palette.mode === 'light' ? 0.09 : 0.08;

  return (
    <Box
      aria-hidden
      sx={{
        position: 'absolute',
        inset: 0,
        pointerEvents: 'none',
        backgroundImage: `
          repeating-linear-gradient(
            to right,
            ${gridColor} 0,
            ${gridColor} 1px,
            transparent 1px,
            transparent 28px
          ),
          repeating-linear-gradient(
            to bottom,
            ${gridColor} 0,
            ${gridColor} 1px,
            transparent 1px,
            transparent 28px
          )
        `,
        opacity: gridOpacity,
        WebkitMaskImage:
          'radial-gradient(ellipse 68% 56% at 50% 45%, rgba(0,0,0,0.96) 0%, rgba(0,0,0,0.72) 40%, rgba(0,0,0,0.28) 66%, rgba(0,0,0,0) 100%)',
        maskImage:
          'radial-gradient(ellipse 68% 56% at 50% 45%, rgba(0,0,0,0.96) 0%, rgba(0,0,0,0.72) 40%, rgba(0,0,0,0.28) 66%, rgba(0,0,0,0) 100%)',
        WebkitMaskRepeat: 'no-repeat',
        maskRepeat: 'no-repeat',
      }}
    />
  );
};

export default JumbotronGrid;
