import { Box, Grid, Typography, useTheme } from '@mui/material';
import PostCard from 'components/cards/PostCard';
import RowList from 'components/RowList';
import { Link } from 'gatsby';
import React from 'react';
import SectionHeader from './SectionHeader';

type Post = {
  id: string;
  fields?: { path?: string | null } | null;
  frontmatter?: {
    title?: string | null;
    subTitle?: string | null;
    date?: string | null;
    categories?: readonly (string | null)[] | null;
    thumbnail?: {
      childImageSharp?: {
        gatsbyImageData?: unknown;
      } | null;
    } | null;
  } | null;
};

interface RecentPostsSectionProps {
  posts: Post[];
  postsPath: string;
}

const RecentPostsSection = ({ posts, postsPath }: RecentPostsSectionProps) => {
  const theme = useTheme();

  return (
    <Box component="section">
      <SectionHeader>
        <Typography variant="h6" fontWeight="bold">
          Recent Posts
        </Typography>
        <Link to={postsPath} style={{ textDecoration: 'none' }}>
          <Typography variant="body2" color="text.secondary">
            View all →
          </Typography>
        </Link>
      </SectionHeader>
      <RowList>
        {posts.map(p => {
          const path = p.fields?.path ?? '/';
          const title = p.frontmatter?.title ?? '';
          const subTitle = p.frontmatter?.subTitle ?? '';
          const thumbnail = p.frontmatter?.thumbnail?.childImageSharp?.gatsbyImageData;
          const date = p.frontmatter?.date ?? '1991-01-01';
          const categories = p.frontmatter?.categories?.filter(Boolean) ?? [];

          return (
            <Grid
              item
              xs={12}
              key={p.frontmatter?.title}
              sx={{
                padding: `${theme.spacing(0.25)} 0`,
                '& a': {
                  textDecoration: 'none',
                  display: 'block',
                  borderRadius: theme.shape.borderRadius * 2,
                  transition: 'background-color 0.2s ease-in-out',
                },
                '& a:hover': {
                  backgroundColor:
                    theme.palette.mode === 'light' ? 'rgba(0, 0, 0, 0.01)' : 'rgba(255, 255, 255, 0.025)',
                },
                '& + &': {
                  borderTop: `1px solid ${theme.palette.divider}`,
                },
              }}
            >
              <Link to={path}>
                <PostCard
                  title={title}
                  subTitle={subTitle}
                  thumbnail={thumbnail}
                  date={date}
                  badge={
                    categories.length > 0 ? (
                      <Typography variant="caption" color="text.secondary">
                        {categories.join(' / ')}
                      </Typography>
                    ) : null
                  }
                />
              </Link>
            </Grid>
          );
        })}
      </RowList>
    </Box>
  );
};

export default RecentPostsSection;
