import { Box, Grid, Typography, styled, useTheme } from '@mui/material';
import { Link } from 'gatsby';
import React from 'react';
import SectionHeader from './SectionHeader';

type SteadySeller = {
  id: string;
  fields?: { path?: string | null } | null;
  frontmatter?: {
    title?: string | null;
    categories?: readonly (string | null)[] | null;
  } | null;
};

interface SteadySellersSectionProps {
  steadySellers: SteadySeller[];
  postsPath: string;
}

const SteadySellersSection = ({ steadySellers, postsPath }: SteadySellersSectionProps) => {
  const theme = useTheme();

  if (steadySellers.length === 0) return null;

  return (
    <Box component="section" sx={{ marginBottom: theme.spacing(6) }}>
      <SectionHeader>
        <Typography variant="h6" fontWeight="bold">
          Steady Sellers
        </Typography>
        <Link to={postsPath} style={{ textDecoration: 'none' }}>
          <Typography variant="body2" color="text.secondary">
            View all →
          </Typography>
        </Link>
      </SectionHeader>
      <Grid container spacing={1}>
        {steadySellers.map((p, i) => {
          const path = p.fields?.path ?? '/';
          const title = p.frontmatter?.title ?? '';
          const categories = p.frontmatter?.categories?.filter(Boolean) ?? [];

          return (
            <Grid item xs={12} md={6} key={p.id}>
              <Link
                to={path}
                style={{ textDecoration: 'none', display: 'block' }}
              >
                <RankedCard>
                  <RankNumber>{i + 1}</RankNumber>
                  <Box sx={{ minWidth: 0 }}>
                    <Typography
                      variant="body2"
                      fontWeight="bold"
                      lineHeight={1.4}
                      color="text.primary"
                      sx={{
                        overflow: 'hidden',
                        textOverflow: 'ellipsis',
                        whiteSpace: 'nowrap',
                      }}
                    >
                      {title}
                    </Typography>
                    {categories.length > 0 && (
                      <Typography variant="caption" color="text.secondary" lineHeight={1.2}>
                        {categories.join(' / ')}
                      </Typography>
                    )}
                  </Box>
                </RankedCard>
              </Link>
            </Grid>
          );
        })}
      </Grid>
    </Box>
  );
};

const RankedCard = styled(Box)(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  gap: theme.spacing(2),
  padding: `${theme.spacing(1.5)} ${theme.spacing(2)}`,
  borderRadius: theme.shape.borderRadius * 2,
  transition: 'background-color 0.2s ease-in-out',
  '&:hover': {
    backgroundColor:
      theme.palette.mode === 'light' ? 'rgba(0, 0, 0, 0.03)' : 'rgba(255, 255, 255, 0.04)',
  },
}));

const RankNumber = styled(Typography)(({ theme }) => ({
  fontWeight: 800,
  fontSize: '1.5rem',
  lineHeight: 1,
  minWidth: '2rem',
  textAlign: 'center',
  color: theme.palette.mode === 'light' ? theme.palette.grey[300] : theme.palette.grey[700],
  flexShrink: 0,
}));

export default SteadySellersSection;
