import React from 'react';
import { graphql, HeadProps, PageProps } from 'gatsby';
import HomePage from 'containers/HomePage';
import SEO from 'components/SEO';
import { getAlternateLanguages } from 'utils/i18nPath';
import type { SupportedLanguages } from 'models/languages';

const HomeTemplate = (props: PageProps<Queries.HomePageQuery, { lang: string }>) => {
  return <HomePage {...props} />;
};

export default HomeTemplate;

export const Head = ({ pageContext, location }: HeadProps<Queries.HomePageQuery, { lang: string }>) => {
  const alternateLanguages = getAlternateLanguages(pageContext.lang as SupportedLanguages, '/');

  return (
    <SEO
      description={
        pageContext.lang === 'ko'
          ? '개발자의 집필 공간. 표상에서 그치는 것이 아닌, 본질을 탐구하고 사유하는 삶.'
          : 'A collection of posts by Evan Moon on software, engineering culture, and thoughtful problem solving.'
      }
      lang={pageContext.lang}
      pathname={location.pathname}
      alternateLanguages={alternateLanguages}
      includeXDefaultAlternate={true}
    />
  );
};

export const pageQuery = graphql`
  query HomePage($lang: String!) {
    books: allMarkdownRemark(
      filter: { fields: { mdType: { eq: "books" }, lang: { eq: $lang } } }
      sort: { frontmatter: { date: DESC } }
      limit: 7
    ) {
      edges {
        node {
          ...Book
        }
      }
    }
    steadySellers: allMarkdownRemark(
      filter: {
        fields: { mdType: { eq: "posts" }, lang: { eq: $lang }, steadySellerRank: { gt: 0 } }
      }
      sort: { fields: { steadySellerRank: ASC } }
      limit: 10
    ) {
      edges {
        node {
          ...Post
        }
      }
    }
    posts: allMarkdownRemark(
      filter: { fields: { mdType: { eq: "posts" }, lang: { eq: $lang } } }
      sort: { frontmatter: { date: DESC } }
      limit: 10
    ) {
      edges {
        node {
          ...Post
        }
      }
    }
  }
`;
